import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Evidencia_resposta_exec } from '../../../modelo/Evidencia_resposta_exec'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'evidencia_resposta_exec-dados-edit',   templateUrl: 'evidencia_resposta_exec_dadosEdit.component.html' }) export class Evidencia_resposta_execDadosEditComponent implements OnInit {    evidencia_resposta_exec: Evidencia_resposta_exec;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.evidencia_resposta_exec.Evidencia_resposta_exec";                 let id = +params['id'];         this.evidencia_resposta_exec = new Evidencia_resposta_exec();         this.evidencia_resposta_exec.cd_evidencia_resposta_exec = id;         o.objetoJson = this.evidencia_resposta_exec;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a evidencia_resposta_exec. ';       }      this.evidencia_resposta_exec = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


